.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
}

.card__logo {
  background-image: url("imac.202c52ff.jpeg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
}

.card__description {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  text-align: left;
  color: #616070;
  width: 100%;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__stars {
  margin-bottom: 24px;
}

.card__price {
  justify-content: space-between;
  width: 100%;
  margin-bottom: 16px;
  display: flex;
}

.card__price-name {
  text-align: left;
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-amount {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  display: flex;
}

.card__button-link {
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
}

.card__button:hover {
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card__button-link:hover {
  color: #00acdc;
}

.stars {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__reviews {
  text-align: right;
  color: #060b35;
  margin-top: 2px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

body {
  margin: 0;
}

html {
  font-family: Roboto, sans-serif;
}
